import _ from 'lodash';
import AILabel from 'ailabel';

import {Point, Image, Label} from './interface';

// 原点位于图片左上角 ——> 中心点为原点的坐标
export function imageToCenterPointsForPoint(points: Point[], image: Image) {
    const {width: imgWidth, height: imgHeight} = image;
    return _.map(points, point => {
        const {x, y} = point;
        return (imgWidth && imgHeight)
            ? {
                x: x - imgWidth / 2,
                y: imgHeight / 2 - y
            }
            : {x, y};
    });
}

// 原点位于图片左上角 ——> 中心点为原点的坐标
export function imageToCenterPointsForMask(masks: any[], image: Image) {
    const {width: imgWidth, height: imgHeight} = image;
    return _.map(masks, mask => {
        const [pos, color] = mask;
        const [x, y] = pos;
        const formatPosition = (imgWidth && imgHeight)
            ? {
                x: Math.ceil(x - imgWidth / 2),
                y: Math.ceil(imgHeight / 2 - y)
            }
            : {x, y};
        return [
            [formatPosition.x, formatPosition.y],
            color
        ];
    });
}

export function toFixed(num: number | string) {
    return parseFloat((+num).toFixed(4));
}

// 中心点为原点的坐标 ——> 原点位于图片左上角
export function centerToImagePointsForPoint(points: Point[], image: Image) {
    const {width: imgWidth, height: imgHeight} = image;
    return _.map(points, point => {
        const {x, y} = point;
        return (imgWidth && imgHeight)
            ? {
                x: x + imgWidth / 2,
                y: imgHeight / 2 - y
            }
            : {x, y};
    });
}

// 中心点为原点的坐标 ——> 原点位于图片左上角
export function centerToImagePointsForMask(masks: any[], image: Image) {
    const {width: imgWidth, height: imgHeight} = image;
    return _.map(masks, mask => {
        const [pos, color] = mask;
        const [x, y] = pos;
        const formatPosition = (imgWidth && imgHeight)
            ? {
                x: Math.floor(x + imgWidth / 2),
                y: Math.floor(imgHeight / 2 - y)
            }
            : {x, y};
        return [
            [formatPosition.x, formatPosition.y],
            color
        ];
    });
}

// 获取标注标签名称、删除按钮的位置坐标
export function getLabelLabelDeletePosition(featureType: string, type: string, points: Point[]) {
    const defaultPosition = {x: 0, y: 0};
    if (featureType === 'rect') {
        const bounds = AILabel.Util.getBounds(points);
        const [leftTopPoint = defaultPosition, rightTopPoint = defaultPosition] = bounds;
        // 矩形：label位于左上角，delete位于右上角
        return type === 'label' ? leftTopPoint : rightTopPoint;
    }
    if (featureType === 'polygon') {
        const sortedPoints = _.sortBy(points, ['y']);
        const pointsLength = sortedPoints.length;
        return pointsLength ? sortedPoints[pointsLength - 1] : defaultPosition;
    }
    if (featureType === 'polyline' || featureType === 'point') {
        const pointsLength = points.length;
        return pointsLength ? points[pointsLength - 1] : defaultPosition;
    }
    return defaultPosition;
}

// 根据key获取对应的label
export function getLabelWithKey(labels: Label[], labelKey: any) {
    const mapLabel = _.find(labels, {key: labelKey});
    return _.cloneDeep(mapLabel);
}

// 格式化前后labels差异label【产出toAdd\toUpdate\toDelete三项数据】
export function diffLabels(labels: Label[] = [], nextLabels: Label[] = []) {
    const toRemoveLabels: Label[] = []; // 待删除
    const toUpdateLabels: Label[] = []; // 待更新
    const toAddLabels: Label[] = []; // 待添加
    _.each(labels, label => {
        const nextLabel = _.find(nextLabels, {key: label.key});
        if (nextLabel) {
            // 此时需要判断是否触发update
            const isLabelEqual = _.isEqual(label, nextLabel);
            if (!isLabelEqual) {
                // 说明需要触发update的标注框
                toUpdateLabels.push(nextLabel);
            }
        }
        else {
            // 说明是删除labels
            toRemoveLabels.push(label);
        }
    });

    _.each(nextLabels, nextLabel => {
        const label = _.find(labels, {key: nextLabel.key});
        if (!label) {
            toAddLabels.push(nextLabel);
        }
    });
    return {
        toRemoveLabels,
        toUpdateLabels,
        toAddLabels
    };
}

// 递归遍历修改function至string方便进行比较
export function processObjFunction(obj: any) {
    let result = _.isArray(obj) ? [] : {};
    for (let key in obj) {
        if (obj.hasOwnProperty(key)) {
            if (_.isObject(obj[key])) {
                result[key] = processObjFunction(obj[key]);
            }
            else if (_.isFunction(obj[key])) {
                result[key] = obj[key].toString();
            }
            else {
                result[key] = obj[key];
            }
        }
    }
    return result;
}
