/**
 * @file components/HooksLabelBox
 * @author dingyang
 */

import _ from 'lodash';
import React, {useMemo} from 'react';

import {HooksLabelBoxProps} from '../../hooks/labelbox/interface';

import './index.less';

export default React.forwardRef((props: HooksLabelBoxProps) => {
    const {outerContainerId, containerId, wrapClassName, container} = props;
    const customWidth = _.get(container, 'width');
    const customHeight = _.get(container, 'height');

    const customStyle = useMemo(() => {
        const customSize: any = {};
        if (customWidth) {
            const styleWidth = _.isNumber(customWidth) ? `${customWidth}px` : customWidth;
            customSize.width = styleWidth;
        }
        if (customHeight) {
            const styleHeight = _.isNumber(customHeight) ? `${customHeight}px` : customHeight;
            customSize.height = styleHeight;
        }
        return customSize;
    }, [customWidth, customHeight]);

    return (
        <div id={outerContainerId} className={`aibp-ailabel-core ${wrapClassName}`} style={customStyle}>
            <div id={containerId} className={'aibp-ailabel-core-container'} />
        </div>
    );
});
