/**
 * @file 标注组件-标注集合
 * @author dingyang(dingyang@baidu.com)
 */

import _ from 'lodash';
import {useEffect, useCallback, useRef, useState} from 'react';

import {diffLabels} from './util';
import {Label} from './interface';

export default (
    gMap: any,
    labels: Label[],
    gFeatureLayer: any | null,
    gTextLayer: any | null
) => {
    const initPreLabels: Label[] = [];
    const refContainer = useRef(initPreLabels);
    const refShowLabels = useRef<Function>();

    const [toRemoveLabels, setToRemoveLabels] = useState<Label[]>([]);
    const [toUpdateLabels, setToUpdateLabels] = useState<Label[]>([]);
    const [toAddLabels, setToAddLabels] = useState<Label[]>([]);

    // 展示标注对象【矩形|多边形】
    refShowLabels.current = useCallback(() => {
        // 获取&保存refContainer.current【即上一次labels数据】
        const preLabels = _.cloneDeep(refContainer.current);
        refContainer.current = _.cloneDeep(labels);

        const {
            toRemoveLabels,
            toUpdateLabels,
            toAddLabels
        } = diffLabels(preLabels, labels);

        setToRemoveLabels(toRemoveLabels);
        setToUpdateLabels(toUpdateLabels);
        setToAddLabels(toAddLabels);
    }, [labels]);

    // 监听标注对象数据变化
    useEffect(() => {
        refShowLabels.current && refShowLabels.current();
    }, [labels]);
    useEffect(() => {
        refContainer.current = [];
        refShowLabels.current && refShowLabels.current();
    }, [gMap, gFeatureLayer, gTextLayer]);

    return {
        toRemoveLabels,
        toUpdateLabels,
        toAddLabels
    };
};
