/**
 * @file hooks/useTableBar 表格上方操作区
 * @author xuemin02
 */

import React from 'react';
import _ from 'lodash';

export interface TableBarProps {
    leftConfig?: any,
    rightConfig?: any
}

export default (props: TableBarProps) => {
    const {leftConfig = [], rightConfig = []} = props;

    const getConfigComp = (config: any) => {
        return _.map(config, configItem => {
            const {key, control: ControlComp, children, config} = configItem;
            return (
                <ControlComp key={key} {...config}>
                    {children}
                </ControlComp>
            );
        });
    };

    const tableBarComp = (
        <div className="aibp-table-bar">
            <div className="aibp-table-bar-left">
                {getConfigComp(leftConfig)}
            </div>
            <div className="aibp-table-bar-right">
                {getConfigComp(rightConfig)}
            </div>
        </div>
    );

    return {tableBarComp};
};
