/**
 * @file Uploader 上传
 * @author xuemin02
 */

import _ from 'lodash';
import React, {useCallback, useState, ReactNode} from 'react';
import {Button, Upload} from 'antd';
import {PlusOutlined} from '@ant-design/icons';
import classnames from 'classnames';
import {useOriginalDeepCopy} from '@huse/previous-value';
import {UploadProps, UploadFile, UploadChangeParam} from 'antd/es/upload/interface';

import Card from './Card';
import {CLOUD_BASE64} from '../../constants';

import './index.less';

const MAX_FILE_NUM = 100;

export interface UploaderProps extends UploadProps {
    wrapClassName?: string,
    className?: string,
    extra?: string | ReactNode,
    maxFileNum?: number,
    children?: ReactNode,
    customItem?: ReactNode,
    onClose?: (file: UploadFile) => void
}

export default (props: UploaderProps) => {
    const {
        wrapClassName = '',
        className = '',
        extra = '',
        listType = 'text',
        maxFileNum = MAX_FILE_NUM,
        fileList = [],
        showUploadList = {},
        iconRender = null,
        children = null,
        customItem = null,
        onChange,
        onClose,
        ...restProps
    } = props;

    const isPictureType = listType === 'picture-card';

    const originFileList = useOriginalDeepCopy(fileList);

    const [totalFileList, setTotalFileList] = useState<UploadFile<any>[]>([]);

    const handleUploadChange = useCallback((fileData: UploadChangeParam) => {
        _.isFunction(onChange) && onChange(fileData);
        setTotalFileList(fileData.fileList);
    }, [onChange]);
    // picture-card模式下删除文件
    const handleClose = useCallback((file: UploadFile) => {
        setTotalFileList(prevTotalFileList => {
            const currentFileList = _.filter(prevTotalFileList, item => item.uid !== file.uid);
            file.status = 'removed';
            handleUploadChange({file, fileList: currentFileList});
            return currentFileList;
        });
        _.isFunction(onClose) && onClose(file);
    }, [onClose, handleUploadChange]);

    const realFileList = props.fileList ? originFileList : totalFileList;
    const isFileListEmpty = _.isEmpty(realFileList);
    const uploadWrapClassName = classnames('aibp-uploader-container', `${wrapClassName}`);
    const uploadClassName = classnames(
        'aibp-uploader', {['aibp-uploader-card']: !isFileListEmpty && isPictureType}, `${className}`
    );

    return (
        <div className={uploadWrapClassName}>
            <div className={uploadClassName}>
                {isPictureType && (<div className={`uploader-action ${!isFileListEmpty ? 'uploader-action-card' : ''}`}>
                    {isFileListEmpty && <img className='cloud-img' src={CLOUD_BASE64} />}
                    <div className='uploader-card-list'>
                        {_.map(realFileList, file => (<Card
                            key={file.uid}
                            file={file}
                            iconRender={iconRender}
                            onClose={() => handleClose(file)}
                        />))}
                        <Upload
                            className='uploader'
                            showUploadList={false}
                            fileList={realFileList}
                            onChange={handleUploadChange}
                            {...restProps}
                        >
                            {isFileListEmpty
                                ? (children || <Button type='primary'>点击选择文件</Button>)
                                : (totalFileList.length >= maxFileNum
                                    ? null
                                    : (<div className='continue-upload'>
                                        <PlusOutlined />
                                        <p>继续添加</p>
                                    </div>)
                                )}
                        </Upload>
                    </div>
                    {extra && isFileListEmpty && <div className='uploader-extra'>{extra}</div>}
                </div>)}
                {listType === 'text' && <div className='uploader-action'>
                    <img className='cloud-img' src={CLOUD_BASE64} />
                    <Upload
                        className='uploader'
                        showUploadList={showUploadList}
                        fileList={totalFileList}
                        onChange={handleUploadChange}
                        {...restProps}
                    >
                        {children || <Button type='primary'>点击选择文件</Button>}
                    </Upload>
                    {extra && <div className='uploader-extra'>{extra}</div>}
                </div>}
            </div>
            {customItem}
        </div>
    );
};
