/**
 * @file hooks/useFormSubmit 表单提交处理
 * @author xuemin02
 */

import _ from 'lodash';
import {useState} from 'react';

export interface FormSubmitProps {
    action: (params?: any) => any,
    form: any,
    $onRequest?: (params?: any) => any,
    $onResponse?: (params?: any) => any,
    $onError?: (params?: any) => any,
}

export default (props:FormSubmitProps) => {
    const {action, form, $onRequest, $onResponse, $onError} = props;

    const [submitting, setSubmitting] = useState(false);

    const submit = () => {
        return new Promise((resolve, reject) => {
            form.validateFields()
                .then(async (values: any) => {
                    setSubmitting(true);
                    const payload = _.isFunction($onRequest) ? ($onRequest(values) || values) : values;
                    const response = await action(payload);
                    $onResponse && $onResponse(response);
                    setSubmitting(false);
                    resolve(response);
                })
                .catch((error: any) => {
                    $onError && $onError(error);
                    setSubmitting(false);
                    reject(error);
                });
        });
    };

    return {submit, submitting};
};
