/**
 * @file 标注组件-涂抹集合
 * @author dingyang(dingyang@baidu.com)
 */

import {useEffect} from 'react';

export default (
    gMap: any,
    masks: any[],
    image: any,
    gMaskLayer: any
) => {
    // 监听标注对象数据变化
    useEffect(() => {
        if (!gMap || !gMaskLayer) {
            return;
        }
        gMaskLayer.addMasks(masks, true);
    }, [gMap, masks, image, gMaskLayer]);
};
