/**
 * @file components/HooksTable
 * @author xuemin02
 */

import _ from 'lodash';
import React, {Fragment} from 'react';
import {Table, Spin, Pagination} from 'antd';

import {EMPTY_DATA_IMG} from '../../constants';

const OMIT_FILTER_KEYS = ['pageNo', 'pageSize', 'order', 'orderBy'];

export interface TableProps {
    loading?: boolean;
    pageNo: number;
    pageSize: number;
    totalCount: number;
    dataSource: any[];
    filterData?: object;
    selectedRowKeys?: string[];
    card?: any;
    columns?: any[];
    pagination?: object;
    select?: 'multi' | 'single' | 'none';
    locale?: object;
    onTableChange?: (pagination: object, filters: any, sorter: any, extra: any) => void;
    onSelectedRowKeysChange?: (selectedRowKeys: any[]) => void;
    onPaginationChange?: (pageNo: number, pageSize: number) => void;
    onPageSizeChange?: (pageNo: number, pageSize: number) => void;
}

export default (props: TableProps) => {
    const {
        loading,
        pageNo,
        pageSize,
        totalCount,
        dataSource,
        filterData,
        selectedRowKeys,
        card: Card,
        columns,
        pagination: propsPagination,
        select = 'multi',
        locale: propsLocale = {},
        onTableChange,
        onPaginationChange,
        onPageSizeChange,
        onSelectedRowKeysChange,
        ...restTableProps
    } = props;

    const {omitFilterKeys = [], descriptions, defaultDescriptions, ...restPropsLocale} = propsLocale as any;
    const tableFilterData = _.omit(filterData, [...OMIT_FILTER_KEYS, ...omitFilterKeys]);
    const tableFilterValue = _.values(tableFilterData);
    const hasTableFilterValue = _.some(tableFilterValue, valueItem => !!valueItem);

    const curDescriptions = (descriptions && !hasTableFilterValue)
        ? descriptions
        : (defaultDescriptions || ['暂无数据']);

    const pagination = propsPagination || (propsPagination !== false && {
        total: totalCount,
        current: pageNo,
        pageSize,
        showSizeChanger: true,
        showQuickJumper: true,
        onChange: onPaginationChange,
        ...(Card ? {
            onShowSizeChange: onPageSizeChange
        } : {})
    });

    const locale = {
        emptyText: (
            <div className="ant-empty ant-empty-normal aibp-empty">
                <img className="aibp-empty-image" src={EMPTY_DATA_IMG} width={140} />
                <div className="ant-empty-description aibp-empty-description">
                    {_.map(curDescriptions, description => <div>{description}</div>)}
                </div>
            </div>
        ),
        ...restPropsLocale
    };

    const rowSelection = select === 'none'
        ? undefined
        : {
            selectedRowKeys,
            // type: select === 'single' ? 'radio' : 'checkbox', // todo 暂不支持type，ts会报错
            onChange: onSelectedRowKeysChange
        };

    return Card ? (
        <div className="aibp-list-card">
            <Spin spinning={loading}>
                {!loading && dataSource.length ? (
                    <Fragment>
                        <div className="aibp-list-card-container">
                            {_.map(dataSource, item => (
                                <div className="aibp-list-card-item">
                                    <Card
                                        item={item}
                                        rowSelection={rowSelection}
                                        {...restTableProps}
                                    />
                                </div>
                            ))}
                        </div>
                        {pagination && (
                            <div className="aibp-list-card-pagination">
                                <Pagination {...pagination} />
                            </div>
                        )}
                    </Fragment>
                ) : (locale && locale.emptyText)}
            </Spin>
        </div>

    ) : (
        <Table
            className="aibp-list-table"
            loading={loading}
            dataSource={dataSource}
            columns={columns}
            pagination={pagination}
            locale={locale}
            rowSelection={rowSelection}
            showSorterTooltip={false}
            onChange={onTableChange}
            {...restTableProps}
        />
    );
};
