import React, {ReactNode} from 'react';
import _ from 'lodash';
import {Menu} from 'antd';

import './index.less';

const isDev = process.env.NODE_ENV === 'development';
// 在生产环境中，使用react-router-dom，没有问题
// 在展示demo时，需要使用react-router，不然会报错，似乎是bisheng依赖的react-router-dom有问题
// https://github.com/sorrycc/blog/issues/82
const {Link} = require(isDev ? 'react-router' : 'react-router-dom');

export interface OptionProps {
    key?: string,
    title: string | ReactNode,
    link: string | object | ((location: any) => {})
}

export interface SidebarProps {
    options?: Array<OptionProps>,
    activeKey?: string
}

export default (props: SidebarProps) => {
    const {options = [], activeKey} = props;

    return (
        <Menu
            className="aibp-detail-content-sidebar"
            mode="inline"
            selectedKeys={activeKey ? [activeKey] : []}
        >
            {_.map(options, ({key, title, link}, optionIndex) => (
                <Menu.Item key={key || optionIndex}>
                    <Link to={link}>{title}</Link>
                </Menu.Item>
            ))}
        </Menu>
    );

};
