/**
 * @file BackTo 返回
 * @author xuemin02
 */

import _ from 'lodash';
import React, {ReactNode, useCallback} from 'react';
import {Button} from 'antd';
import classnames from 'classnames';
import {LeftOutlined} from '@ant-design/icons';

import './index.less';

const isDev = process.env.NODE_ENV === 'development';
// 在生产环境中，使用react-router-dom，没有问题
// 在展示demo时，需要使用react-router，不然会报错，似乎是bisheng依赖的react-router-dom有问题
// https://github.com/sorrycc/blog/issues/82
const {Link} = require(isDev ? 'react-router' : 'react-router-dom');

const BACK_TYPES = {
    ARROW: 'arrow',
    BUTTON: 'button'
};

const TITLE_POSITIONS = {
    CENTER: 'center',
    LEFT: 'left',
    RIGHT: 'right'
};

export interface ButtonConfigProps {
    key?: string,
    disabled?: boolean,
    text?: string | ReactNode,
    action?: () => void
}

export interface OptionProps {
    backText?: string | ReactNode,
    title?: string | ReactNode,
    titlePosition?: 'center' | 'left' | 'right',
    titleComp?: string | ReactNode,
    buttonConfig?: Array<ButtonConfigProps>
}

export interface BackToProps {
    className?: string,
    type?: 'arrow' | 'button',
    to?: string | false,
    option?: OptionProps
}

export default (props: BackToProps) => {
    const {type = BACK_TYPES.ARROW, to = '/', option = {}, className} = props;

    const {title, titlePosition = TITLE_POSITIONS.CENTER, titleComp, backText, buttonConfig} = option as any;

    const handlButtonAction = useCallback(action => {
        action && action();
    }, []);

    const backToClassName = classnames('aibp-back-to', className);

    return (
        <div className={backToClassName}>
            <div className="aibp-back-to-left">
                <div className={`aibp-back-to-icon aibp-back-to-icon-${type}`}>
                    {to && <Link to={to}><LeftOutlined /></Link>}
                </div>
                {backText && (
                    <div className="aibp-back-to-text">
                        {to && type === BACK_TYPES.ARROW ? <Link to={to}>{backText}</Link> : backText}
                    </div>
                )}
                {titlePosition === TITLE_POSITIONS.LEFT && !titleComp && title && (
                    <div className="aibp-back-to-title">{title}</div>
                )}
                {buttonConfig && (
                    <div className="aibp-back-to-button-group">
                        {_.map(buttonConfig, (button, buttonIndex) => {
                            const {key, disabled, text, action} = button;
                            return (
                                <Button
                                    key={key || buttonIndex}
                                    className="aibp-back-to-button"
                                    disabled={disabled}
                                    onClick={() => handlButtonAction(action)}
                                >{text}</Button>
                            );
                        })}
                    </div>
                )}
            </div>
            <div className="aibp-back-to-main">
                {titleComp && <div className="aibp-back-to-title">{titleComp}</div>}
                {!titleComp && titlePosition !== TITLE_POSITIONS.LEFT && title && (
                    <div className={`aibp-back-to-title aibp-back-to-title-${titlePosition}`}>{title}</div>
                )}
            </div>
        </div>
    );
};
