/**
 * @file component/common/GridViews
 * @author xuemin02
 */

import _ from 'lodash';
import React, {ReactNode} from 'react';
import {Row, Col} from 'antd';
import classnames from 'classnames';

import './index.less';

const LABEL_WIDTH = 100;
const COL_COUNT = 2;
const WITH_COLON = true;


export interface ItemOptionsProps {
    key?: string | number,
    label?: string | ReactNode
    content?: string | ReactNode
}

export interface OptionsProps {
    key?: string | number,
    title?: string | ReactNode,
    className?: string,
    labelWidth?: number | string,
    colCount?: number,
    withColon?: boolean,
    options?:  Array<ItemOptionsProps>
}

export interface GridViewsProps {
    className?: string,
    labelWidth?: number | string,
    colCount?: number,
    withColon?: boolean,
    options?:  Array<OptionsProps>
}

const GridViews = (props: GridViewsProps) => {
    const {
        className,
        labelWidth = LABEL_WIDTH,
        colCount = COL_COUNT,
        withColon = WITH_COLON,
        options = []
    } = props;

    const gridViewsClassName = classnames('aibp-grid-views', className);

    return (
        <div className={gridViewsClassName}>
            {_.map(options, (option, optionIndex) => {
                const {key, title} = option;
                return (
                    <GridViews.Item
                        key={key || optionIndex}
                        title={title}
                        className={option.className}
                        colCount={option.colCount || colCount}
                        labelWidth={option.labelWidth || labelWidth}
                        withColon={option.withColon || withColon}
                        options={option.options}
                    />
                );
            })}
        </div>
    );
};

GridViews.Item = (props: OptionsProps) => {
    const {
        title,
        className,
        colCount = COL_COUNT,
        labelWidth = LABEL_WIDTH,
        withColon = WITH_COLON,
        options = []
    } = props;
    const spanCount = Math.ceil(24 / colCount);
    const rowList = _.chunk(options, colCount);
    const gridViewItemClassName = classnames('aibp-grid-view-item', className);

    return (
        <div className={gridViewItemClassName}>
            {title && <div className="aibp-grid-view-item-title">{title}</div>}
            {_.map(rowList, (chunkRow, chunkRowIndex) => (
                <Row key={chunkRowIndex} className="aibp-grid-view-item-row">
                    {_.map(chunkRow, (col, colIndex) => {
                        const {key, label, content} = col;
                        return (
                            <Col span={spanCount} key={key || colIndex} className="aibp-grid-view-item-col">
                                <div className="grid-view-item-label" style={{width: labelWidth}}>
                                    {label}{withColon && ':'}
                                </div>
                                <div className="grid-view-item-content">
                                    {content}
                                </div>
                            </Col>
                        );
                    })}
                </Row>
            ))}
        </div>
    );
};

export default GridViews;