/**
 * @file 标注组件-gImageLayer中grid辅助网格
 * @author dingyang(dingyang@baidu.com)
 */

import _ from 'lodash';
import {useEffect} from 'react';

export default (
    gImageLayer: any,
    image: any
) => {
    // 为ailabel计算zoom值，保证图片初始化能够全contain在容器中
    useEffect(() => {
        if (!gImageLayer) {
            return;
        }
        const gridConfig = _.get(image, 'grid');
        gImageLayer.removeGrid(); // 首先需要删除原有网格
        if (_.isObject(gridConfig)) {
            gImageLayer.update({grid: gridConfig});
        }
    }, [gImageLayer, image]);
};
