/**
 * @file useTheme 利用less的modifyVar修改less变量
 * @author xuemin02
 */

import loadCss from '../../utils/loadCss';
import loadScript from '../../utils/loadScript';
import {noop} from '../../constants';

export interface ThemeProps {
    cssPath: string;
    jsPath: string;
    onComplete?: () => void;
}

export default (props?: ThemeProps) => {
    const {cssPath, jsPath, onComplete = noop} = props || {};

    if (cssPath && !(window as any).aibpThemeCssLoaded) {
        loadCss(cssPath);
        (window as any).aibpThemeCssLoaded = true;
    }

    if (jsPath && !(window as any).less) {
        (window as any).less = {
            async: true,
            javascriptEnabled: true
        };
        loadScript(jsPath);
    }

    const setTheme = (theme: object) => {
        if ((window as any).less) {
            (window as any).less.modifyVars(theme).then(onComplete);
        }
        else {
            throw new Error('`window.less` is required');
        }
    };

    return {setTheme};
};
