/**
 * @file 标注组件-对外暴露工具方法
 * @author dingyang(dingyang@baidu.com)
 */

import _ from 'lodash';
import {useCallback, useRef} from 'react';
import AILabel from 'ailabel';

import {
    LABEL_RELATED_MARKER_ID_PREFIX,
    LABEL_RELATED_TEXT_ID_PREFIX,
    LABEL_DELETE_MARKER_ID_PREFIX,
    DELETE_ICON,
    DELETE_ICON_OFFSET,
    GTEXT_STYLE
} from './constants';
import {
    getLabelWithKey,
    centerToImagePointsForPoint,
    imageToCenterPointsForPoint,
    getLabelLabelDeletePosition
} from './util';
import {Point, Image, Label, LabelChangeHandler} from './interface';

export type worldToScreenType = (points: Point[]) => Point[];

export default (
    gMap: any,
    gTextLayer: any,
    labels: Label[],
    image: Image | any,
    deleteIcon: string | any,
    onChange: LabelChangeHandler | any
) => {
    const onChangeRef = useRef<any>(null);
    onChangeRef.current = onChange;
    // 坐标系转换
    const worldToScreen = useCallback((points: Point[]) => {
        if (!gMap || !points || !points.length) {
            return points;
        }
        const formatWorldCenterPoints = imageToCenterPointsForPoint(points, image);
        const screenPoints = _.map(formatWorldCenterPoints,
            (point: Point) => AILabel.Util.worldToScreen(gMap, point.x, point.y));
        return screenPoints;
    }, [gMap, image]);

    // 坐标系转换[屏幕坐标转换为实际坐标]
    const screenToWorld = useCallback((points: Point[], returnType?: String) => {
        if (!gMap || !points || !points.length) {
            return points;
        }
        const worldPoints = _.map(points,
            (point: Point) => AILabel.Util.screenToWorld(gMap, point.x, point.y));

        // 代表AILabel坐标系下坐标，非image坐标系坐标
        if (returnType && returnType === 'center') {
            return worldPoints;
        }
        const formatWorldCenterPoints = centerToImagePointsForPoint(worldPoints, image);
        return formatWorldCenterPoints;
    }, [gMap, image]);

    // 删除标注lable关联markers
    const removeRelatedMarkers = useCallback((removeLabel: Label) => {
        if (!gMap) {
            return;
        }
        const {key: featureId} = removeLabel;
        const allMarkers = gMap.mLayer.getAllMarkers(); // 返回所有标注数据
        const deleteRelatedMarkers = _.filter(
            allMarkers,
            marker => marker.id.indexOf(`${LABEL_RELATED_MARKER_ID_PREFIX}${featureId}-`) === 0
        );
        const toRemoveRelatedMarkerIds = _.map(deleteRelatedMarkers, 'id');
        if (toRemoveRelatedMarkerIds.length) {
            gMap.mLayer.removeMarkersByIds(toRemoveRelatedMarkerIds);
        }
    }, [gMap]);

    // 删除标注lable关联texts
    const removeRelatedTexts = useCallback((removeLabel: Label) => {
        if (!gMap || !gTextLayer) {
            return;
        }
        const {key: featureId} = removeLabel;
        const allTexts = gTextLayer.getAllTexts(); // 返回所有文本数据
        const deleteRelatedTexts = _.filter(
            allTexts,
            text => text.id.indexOf(`${LABEL_RELATED_TEXT_ID_PREFIX}${featureId}-`) === 0
        );
        const toRemoveRelatedTextIds = _.map(deleteRelatedTexts, 'id');
        if (toRemoveRelatedTextIds.length) {
            gTextLayer.removeTextsByIds(toRemoveRelatedTextIds);
        }
    }, [gMap, gTextLayer]);

    // 删除标注lable关联texts&markers
    const removeRelatedMarkersAndTexts = useCallback((removeLabel: Label) => {
        removeRelatedMarkers(removeLabel);
        removeRelatedTexts(removeLabel);
    }, [removeRelatedMarkers, removeRelatedTexts]);

    // 添加标注删除按钮【注：只有在label: {select: true}才需要添加】
    const addLabelDeleteIcon = useCallback((featureId: any, label: Label) => {
        if (!gMap) {
            return;
        }
        // 数据准备
        const {select = false, points = [], type = 'rect'} = label;
        const deleteMarkerId = `${LABEL_DELETE_MARKER_ID_PREFIX}${featureId}`;
        gMap.mLayer.removeMarkerById(deleteMarkerId); // 首先做一下删除操作
        // 如果当前label没有被选中或者不存在feature对象
        if (!select) {
            return;
        }
        // 如果当前label被选中【即select:true】
        const formatPoints = imageToCenterPointsForPoint(points, image);
        const deleteIconPoint = getLabelLabelDeletePosition(type, 'delete', formatPoints);

        const deleteMarker = new AILabel.Marker(deleteMarkerId, {
            src: deleteIcon || DELETE_ICON,
            x: deleteIconPoint.x,
            y: deleteIconPoint.y,
            offset: DELETE_ICON_OFFSET[type]
        });
        gMap.mLayer.addMarker(deleteMarker);

        deleteMarker.regEvent('click', () => {
            const toDeleteLabel = getLabelWithKey(labels, featureId);
            if (toDeleteLabel && onChangeRef.current) {
                onChangeRef.current('delete', toDeleteLabel);
            }
        });
    }, [gMap, image, labels, deleteIcon]);

    // 删除标注删除按钮【注：只有在label: {select: false}才需要添加】
    const removeLabelDeleteIcon = useCallback((featureId: any) => {
        if (!gMap) {
            return;
        }
        const deleteMarkerId = `${LABEL_DELETE_MARKER_ID_PREFIX}${featureId}`;
        gMap.mLayer.removeMarkerById(deleteMarkerId); // 首先做一下删除操作
    }, [gMap]);

    // 添加标注绑定marker标记对象
    const addLabelRelatedMarkers = useCallback((featureId: any, label: Label) => {
        if (!gMap) {
            return;
        }
        const {markers = []} = label;
        // 标注框关联标记对象集
        _.each(markers, (marker, index) => {
            const relatedMarkerId = `${LABEL_RELATED_MARKER_ID_PREFIX}${featureId}-${index}`;
            const {
                src = DELETE_ICON,
                position = {x: 0, y: 0},
                offset = {x: 0, y: 0},
                events = {}
            } = marker;
            const tmpMarker = new AILabel.Marker(relatedMarkerId, {
                src,
                ...(imageToCenterPointsForPoint([position], image)[0]),
                offset
            });
            gMap.mLayer.addMarker(tmpMarker);
            if ((events as any).click) {
                tmpMarker.regEvent('click', () => {
                    (events as any).click(tmpMarker, label);
                });
            }
        });
    }, [gMap, image]);
    // 添加标注绑定text文本对象
    const addLabelRelatedTexts = useCallback((featureId: any, label: Label) => {
        if (!gTextLayer) {
            return;
        }
        const {texts = []} = label;
        // 标注框关联文本对象集
        _.each(texts, (text, index) => {
            const relatedTextId = `${LABEL_RELATED_TEXT_ID_PREFIX}${featureId}-${index}`;
            const {
                position = {x: 0, y: 0},
                offset = {x: 0, y: 0},
                maxWidth = 100,
                text: textValue,
                style: tmpStyle
            } = text;
            const textStyle = new AILabel.Style(tmpStyle || GTEXT_STYLE);
            const relatedText = new AILabel.Text(relatedTextId, {
                pos: imageToCenterPointsForPoint([position], image)[0],
                offset,
                maxWidth,
                text: textValue
            }, textStyle);
            gTextLayer.addText(relatedText);
        });
    }, [gTextLayer, image]);

    // 添加标注lable关联texts&markers
    const addRelatedMarkersAndTexts = useCallback((featureId: any, label: Label) => {
        addLabelRelatedMarkers(featureId, label);
        addLabelRelatedTexts(featureId, label);
    }, [addLabelRelatedMarkers, addLabelRelatedTexts]);

    // 删除所有label的删除按钮
    const removeAllDeleteMarkers = useCallback(() => {
        const allMarkers = gMap.mLayer.getAllMarkers(); // 返回所有标注数据
        const deleteIconMarkers = _.filter(
            allMarkers,
            marker => marker.id.indexOf(LABEL_DELETE_MARKER_ID_PREFIX) === 0
        );
        const toRemoveIconMarkerIds = _.map(deleteIconMarkers, 'id');
        if (toRemoveIconMarkerIds.length) {
            gMap.mLayer.removeMarkersByIds(toRemoveIconMarkerIds);
        }
    }, [gMap]);

    // featureReset之后需要对标注层进行清空
    const handleFeatureStatusReset = useCallback(() => {
        removeAllDeleteMarkers();
        if (onChangeRef.current) {
            onChangeRef.current('reset');
        }
    }, [removeAllDeleteMarkers]);

    return {
        worldToScreen, // 坐标系转换
        screenToWorld, // [屏幕坐标转换为实际坐标]
        removeRelatedMarkersAndTexts, // 删除标注关联标记&文本对象集合
        addLabelDeleteIcon, // 添加标注删除icon按钮
        removeLabelDeleteIcon, // 删除标注删除icon按钮
        addRelatedMarkersAndTexts, // 添加标注关联标记&文本对象集合
        removeAllDeleteMarkers, // 删除所有删除icons
        handleFeatureStatusReset // reset操作
    };
};
