/**
 * @file 标注组件-gMap实例化hook
 * @author dingyang(dingyang@baidu.com)
 */

import _ from 'lodash';
import {useState, useEffect, useCallback} from 'react';
import AILabel from 'ailabel';

import {Point} from './interface';

export default (
    containerDom: any,
    image: any,
    initZoom: number
) => {
    // 标注图片基本信息
    const {
        url: imgUrl = '',
        width: imgWidth = 0,
        height: imgHeight = 0
    } = image;

    // 实例化state:gMap
    const [gMap, setGMap] = useState<any>();
    // 实例化state:gImageLayer
    const [gImageLayer, setGImageLayer] = useState<any>();
    // 实例化state:gFeatureLayer
    const [gFeatureLayer, setGFeatureLayer] = useState<any>();
    // 实例化state:gTextLayer
    const [gTextLayer, setGTextLayer] = useState<any>();
    // 实例化state:gMaskLayer
    const [gMaskLayer, setGMaskLayer] = useState<any>();

    // 针对涂抹数据拦截器格式化[image -> center]
    const handleMaskCoordsTransformToCenter = useCallback((point: Point, scene?: String) => {
        const {x, y} = point;
        if (scene === 'mask') {
            return (imgWidth && imgHeight)
                ? {
                    x: Math.ceil(x - imgWidth / 2),
                    y: Math.ceil(imgHeight / 2 - y)
                }
                : {x, y};
        }
        return point;
    }, [imgWidth, imgHeight]);

    // 针对涂抹数据拦截器格式化[center -> image]
    const handleMaskCoordsTransformToImage = useCallback((point: Point, scene?: String) => {
        const {x, y} = point;
        if (scene === 'mask') {
            if (imgWidth && imgHeight) {
                const newX = Math.floor(x + imgWidth / 2);
                const newY = Math.floor(imgHeight / 2 - y);
                return {
                    x: newX === imgWidth ? (newX - 1) : newX,
                    y: newY === imgHeight ? (newY - 1) : newY
                };
            }
            return {x, y};
        }
        return point;
    }, [imgWidth, imgHeight]);

    // 监听容器id，imageBaseInfo, containerSize是否变化
    useEffect(() => {
        // 首先进行错误拦截处理【1、不存在容器dom元素】
        const containerId = _.get(containerDom, 'id', '');
        const containerElement = document.getElementById(containerId);
        if (!containerDom || !containerElement) {
            return;
        }

        // gMap实例化
        const innerGMap = new AILabel.Map(containerId, {zoom: initZoom, cx: 0, cy: 0, autoFeatureSelect: false});

        // 设置涂抹区域
        if (_.isNumber(imgWidth) && _.isNumber(imgHeight)) {
            innerGMap.setMaskBounds(-imgWidth / 2, imgWidth / 2, -imgHeight / 2, imgHeight / 2);
            // 针对绘制完成之后的坐标转换公式【目前只针对mask场景有效】
            innerGMap.slots.on(
                'coordsTransformBeforeRender',
                (point: Point, scene?: String) => handleMaskCoordsTransformToCenter(point, scene)
            );
            // 绘制之前的坐标格式转换公式【目前只针对mask场景有效】
            innerGMap.slots.on(
                'coordsTransformBeforeGeometryDrawDone',
                (point: Point, scene?: String) => handleMaskCoordsTransformToImage(point, scene)
            );
        }

        // gImageLayer实例化添加
        if (imgUrl) {
            const innerGImageLayer = new AILabel.Layer.Image(
                `layer-img-${_.uniqueId()}`,
                imgUrl,
                {w: imgWidth, h: imgHeight},
                {zIndex: 1}
            );
            innerGMap.addLayer(innerGImageLayer);
            setGImageLayer(innerGImageLayer);
        }

        // gFeatureLayer矢量层添加
        const innerGFeatureLayer = new AILabel.Layer.Feature(
            `layer-feature-${_.uniqueId()}`,
            {zIndex: 3, transparent: false}
        );
        innerGMap.addLayer(innerGFeatureLayer);
        setGFeatureLayer(innerGFeatureLayer);

        // gTextLayer文本层添加
        const innerGTextLayer = new AILabel.Layer.Text(`layer-text-${_.uniqueId()}`);
        innerGMap.addLayer(innerGTextLayer);
        setGTextLayer(innerGTextLayer);

        // 掩膜层实例\添加
        const innerGMaskLayer = new AILabel.Layer.Mask(`mask-layer-${_.uniqueId()}`, {zIndex: 2});
        innerGMap.addLayer(innerGMaskLayer);
        setGMaskLayer(innerGMaskLayer);

        // gMap设置
        setGMap(innerGMap);
    }, [
        containerDom,
        imgUrl,
        imgWidth,
        imgHeight,
        initZoom
    ]);

    // 组件卸载，需要进行实例销毁
    useEffect(() => (() => {
        gMap && gMap.destroy();
        setGMap(null);
        setGImageLayer(null);
        setGFeatureLayer(null);
        setGTextLayer(null);
        setGMaskLayer(null);
    }), []);

    // 返回数据体
    return {
        gMap,
        gImageLayer,
        gFeatureLayer,
        gTextLayer,
        gMaskLayer
    };

};
