import React from 'react';
import _ from 'lodash';
import {Form} from 'antd';

export interface FormProps {
    form?: any;
    controls?: Object[];
    className?: string;
}

export interface ControlItemProps {
    title?: string | React.ReactNode,
    name?: string;
    label?: string;
    control: any;
    config?: object;
    events?: object
}

export default (props: FormProps) => {
    const {form, controls, className, ...restFormProps} = props;

    return (
        <Form
            form={form}
            className={`aibp-form ${className}`}
            {...restFormProps}
        >
            {_.map(controls, (controlItemConfig: ControlItemProps) => {
                const {
                    title,
                    name,
                    label,
                    control: Control,
                    config,
                    events = {},
                    ...restControlItemConfig
                } = controlItemConfig;
                return title
                    ? <div className="aibp-form-title">{title}</div>
                    : (
                        <Form.Item
                            key={name}
                            name={name}
                            label={label}
                            {...restControlItemConfig}
                        >
                            <Control {...config} {...events} />
                        </Form.Item>
                    );
            })}
        </Form>
    );
};
