/**
 * @file components/common/ListInput
 * @author zhousheng
 */

import React from 'react';
import {Input, InputNumber, Button, Row, Col} from 'antd';
import _ from 'lodash';
import {
    MinusCircleOutlined,
    PlusOutlined
} from '@ant-design/icons';

import './index.less';

export interface Props {
    valueType: string;
    rules: any;
    values: Array<any>;
    onChange: any;
};
export interface StateObj {
    values: Array<any>;
};

export default class ListInput extends React.Component<Props, StateObj> {
    constructor(props: Props) {
        super(props);
        const {valueType, rules, values} = props;
        const newValues = values.map(v => {
            if (v === 'null' && valueType === 'ListInt') {
                v = rules.minInt || 0;
            }
            else if (v === 'null' && valueType === 'ListDouble') {
                v = rules.minDouble || 0;
            }
            return {
                value: v,
                key: Math.random()
            };
        });
        if (!newValues.length) {
            newValues.push({
                value: valueType === 'ListString' ? '' : rules.minInt || 0,
                key: Math.random()
            });
        }
        this.state = {
            values: newValues
        };
    }
    handleLineMinus = (index: number) => {
        const {onChange} = this.props;
        const values = _.cloneDeep(this.state.values);
        values.splice(index, 1);
        this.setState({
            values
        });
        onChange && onChange(values.map(v => v.value));
    }
    handleLinePlus = () => {
        const {onChange} = this.props;
        const {valueType, rules} = this.props;
        let defaultValue = '';
        if (valueType === 'ListInt') {
            defaultValue = rules.minInt || 0;
        }
        else if (valueType === 'ListDouble') {
            defaultValue = rules.minDouble || 0;
        }
        const values = _.cloneDeep(this.state.values);
        values.push({
            value: defaultValue,
            key: Math.random()
        });
        this.setState({
            values
        });
        onChange && onChange(values.map(v => v.value));
    }
    handleInputChange = (value: string, index: number) => {
        const {onChange} = this.props;
        const values = _.cloneDeep(this.state.values);
        values[index].value = value;
        onChange && onChange(values.map(v => v.value));
        this.setState({
            values
        });
    }
    getInputItem = (value: string, index: number) => {
        const {valueType, rules} = this.props;
        if (valueType === 'ListInt') {
            return (<InputNumber
                min={rules.minInt}
                max={rules.maxInt}
                precision={0}
                value={+value}
                onChange={v => this.handleInputChange(v + '', index)}
            />);
        }
        else if (valueType === 'ListDouble') {
            return (<InputNumber
                min={rules.minDouble}
                max={rules.maxDouble}
                step={rules.step}
                value={+value}
                onChange={v => this.handleInputChange(v + '', index)}
            />);
        }
        return <Input value={value} onChange={e => this.handleInputChange(e.target.value, index)} />;
    }
    render() {
        const {values} = this.state;
        const inputsCom = values.map((v, index) => (
            <Row key={v.key} className="aibp-list-input-line">
                <Col span={18}>
                    {this.getInputItem(v.value, index)}
                </Col>
                <Col span={6} className="minus-button">
                    {values.length > 1 && <MinusCircleOutlined onClick={() => this.handleLineMinus(index)} />}
                </Col>
            </Row>
        ));
        return (<div className="aibp-list-input-box">
            {inputsCom}
            <Row>
                <Col className="aibp-plus-button">
                    <Button icon={<PlusOutlined />} type="dashed" onClick={this.handleLinePlus}>新增一行</Button>
                </Col>
            </Row>
        </div>);
    }
}
