/**
 * @file Uploader card
 * @author maojiaqing
 */

import _ from 'lodash';
import React, {useMemo, useCallback, useState, useEffect, ReactNode} from 'react';
import {Progress} from 'antd';
import classnames from 'classnames';
import {CheckCircleFilled, CloseCircleFilled, ExclamationCircleFilled} from '@ant-design/icons';
import {UploadFile} from 'antd/es/upload/interface';

import utils from '../../utils';
import {TEXT_BASE64, ZIP_BASE64, PDF_BASE64, IMG_BASE64} from '../../constants';

const {getBase64ByFile, getSuffixByName} = utils;

export interface UploaderCardProps {
    file: UploadFile,
    onClose: () => void,
    iconRender: ReactNode
}

export default (props: UploaderCardProps) => {
    const {file, onClose, iconRender} = props;
    const {name, type, status, percent, originFileObj} = file;
    const suffixName = getSuffixByName(name);
    const isImg = /image/.test(type);

    const [isDelete, setIsDelete] = useState<boolean>(false);
    const [imgSrc, setImgSrc] = useState<any>();

    const showBase64 = useMemo(() => {
        if (isImg) {
            return IMG_BASE64;
        }
        if (suffixName === 'pdf') {
            return PDF_BASE64;
        }
        if (_.includes(['zip', 'gz'], suffixName)) {
            return ZIP_BASE64;
        }
        return TEXT_BASE64;
    }, [suffixName, isImg]);

    const renderStatusIcon = useMemo((): ReactNode | null => {
        let statusIcon = null;
        switch (status) {
            case 'done':
                statusIcon = <CheckCircleFilled />;
                break;
            case 'error':
                statusIcon = <ExclamationCircleFilled />;
                break;
            default:
                break;
        }
        return statusIcon;
    }, [status]);

    const renderShowImage = useMemo((): ReactNode | null => {
        let showImage = null;
        if (isImg && imgSrc) {
            showImage = (<div className={`img-box ${!status ? 'no-progress' : ''}`}>
                <img src={imgSrc} />
            </div>);
        }
        else {
            showImage = iconRender;
        }
        return showImage;
    }, [isImg, iconRender, imgSrc, status]);

    const handleMouseEnter = useCallback(() => setIsDelete(true), []);
    const handleMouseLeave = useCallback(() => setIsDelete(false), []);

    useEffect(() => {
        if (originFileObj) {
            getBase64ByFile(originFileObj).then((result: any) => {
                isImg && setImgSrc(result);
            });
        }
    }, [originFileObj, isImg]);

    const uploaderCardClassName = classnames(
        'uploader-card', {'uploader-card-progress-none': !status, 'uploader-card-progress-has': status}
    );
    return (
        <div>
            <div
                className={uploaderCardClassName}
                onMouseEnter={handleMouseEnter}
                onMouseLeave={handleMouseLeave}
            >
                <div className='custom-icon'>
                    {isDelete
                        ? <CloseCircleFilled onClick={onClose} />
                        : ((!status && imgSrc) ? <CheckCircleFilled /> : renderStatusIcon)
                    }
                </div>
                {renderShowImage ? renderShowImage : <img src={showBase64} />}
                {status ? <Progress percent={percent} /> : null}
            </div>
            <div className='card-name' title={name}>{name}</div>
        </div>
    );
};
