/**
 * @file 标注组件-gMap实例对象event事件绑定
 * @author dingyang(dingyang@baidu.com)
 */

import _ from 'lodash';
import {useEffect, useCallback, useRef} from 'react';
import {useUpdate} from 'baidu-acu-react-hooks';
import AILabel from 'ailabel';
import PolyBool from 'polybooljs';
import lineclip from 'lineclip';

import notification from '../../components/notification';

import {
    centerToImagePointsForPoint,
    getLabelWithKey
} from './util';
import {Label, Point} from './interface';
import {LABEL_LABEL_TEXT_ID_PREFIX} from './constants';

export default (
    gMap: any,
    labels: Label[],
    gTextLayer: any,
    image: any,
    show: string,
    filter: boolean,
    clip: boolean,
    onChange: any,
    handleFeatureStatusReset: any
) => {
    const onChangeRef = useRef<any>(null);
    onChangeRef.current = onChange;
    const update = useUpdate();

    // 校验当前绘制的feature矢量对象是否覆盖在标注样本图片范围内
    const filterValidFeature = useCallback((points: Point[]) => {
        const {width: imgWidth, height: imgHeight} = image;
        const [ltPoint, , rbPoint] = AILabel.Util.getBounds(points);
        const halfImageWidth = imgWidth / 2;
        const halfImageHeight = imgHeight / 2;
        const isLTvalid = ltPoint.x >= -halfImageWidth
            && ltPoint.x <= halfImageWidth
            && ltPoint.y >= -halfImageHeight
            && ltPoint.y <= halfImageHeight;
        const isRBvalid = rbPoint.x >= -halfImageWidth
            && rbPoint.x <= halfImageWidth
            && rbPoint.y >= -halfImageHeight
            && rbPoint.y <= halfImageHeight;
        return isLTvalid && isRBvalid;
    }, [image]);

    // 容器范围change
    const handleBoundsChanged = useCallback(() => {
        if (onChangeRef.current) {
            onChangeRef.current('boundsChanged');
        }
    }, []);

    // 要素选择完成回调事件
    const handleFeatureWillSelected = useCallback((feature: any) => {
        const toSelectLabel = getLabelWithKey(labels, feature.id);
        if (toSelectLabel && onChangeRef.current) {
            onChangeRef.current('select', toSelectLabel);
        }
    }, [labels]);

    // 标注绘制完成-callback
    const handleDrawGeometryDone = useCallback((type: string, points: Point[], options: any) => {
        // 存在用户设置过滤并且存在图片背景
        const {url: imgUrl = '', width: imgWidth, height: imgHeight} = image;
        const originPoints = _.isArray(points) ? points : [points];
        let formatPoints = [];
        let dataKey = '';
        if (type === 'mask') {
            formatPoints = originPoints;
            dataKey = 'masks';
        }
        else {
            formatPoints = centerToImagePointsForPoint(originPoints, image);
            dataKey = type === 'point' ? 'point' : 'points';
            if (clip && imgUrl && type !== 'point') {
                // 说明此时需要对多边形进行自动裁剪
                const simplePoints = _.map(formatPoints, fPoint => ([fPoint.x, fPoint.y]));
                if (type === 'polyline') {
                    const imgBBox = [0, 0, imgWidth, imgHeight];
                    const clipPoints = lineclip(simplePoints, imgBBox);
                    if (clipPoints.length) {
                        formatPoints = _.map(clipPoints[0], linePoint => ({x: linePoint[0], y: linePoint[1]}));
                    }
                    else {
                        // 此时交集为空
                        notification.warning('请在‘样本图片区域内’进行标注绘制！');
                        return;
                    }
                }
                else {
                    const imgPoints = [
                        [0, 0],
                        [imgWidth, 0],
                        [imgWidth, imgHeight],
                        [0, imgHeight]
                    ];
                    const newPoints = PolyBool.intersect(
                        {
                            regions: [simplePoints],
                            inverted: false
                        },
                        {
                            regions: [imgPoints],
                            inverted: false
                        }
                    );
                    const firstRegion = _.get(newPoints, 'regions[0]', []);
                    if (firstRegion.length > 2) {
                        formatPoints = _.map(firstRegion, regionPoint => ({x: regionPoint[0], y: regionPoint[1]}));
                    }
                    else {
                        // 首先判断绘制的坐标集合是否位于图形内部，进行不同提示信息
                        const innerImgPoints = _.find(simplePoints, point => {
                            const [x, y] = point;
                            return (x < 0 || x > imgWidth || y < 0 || y > imgHeight);
                        });
                        // 此时交集为空
                        const tipMsg = innerImgPoints
                            ? '请在‘样本图片区域内’进行标注绘制！'
                            : '绘制图形不规则，请重新绘制！';
                        notification.warning(tipMsg);
                        return;
                    }
                }
            }
            else if (filter && imgUrl) {
                const isValidFea = filterValidFeature(originPoints);
                if (!isValidFea) {
                    notification.warning('请在‘样本图片区域内’进行标注绘制！');
                    return;
                }
            }
        }

        if (onChangeRef.current) {
            onChangeRef.current('add', {
                type,
                [dataKey]: type === 'point' ? formatPoints[0] : formatPoints
            }, options);
        }
    }, [image, filter, clip, filterValidFeature]);

    // 编辑涂抹结束事件
    const handleEditMaskDone = useCallback((layersData: any[]) => {
        const defaultLayerMasks = _.get(layersData, '[0].masks', null);
        if (onChangeRef.current && defaultLayerMasks) {
            onChangeRef.current('update', {
                type: 'mask',
                masks: defaultLayerMasks
            });
        }
    }, []);

    // 标注编辑（框平移|节点修改）完成-callback
    const handleEditGeometryDone = useCallback((activeFeature: any, points: Point[]) => {
        // 存在用户设置过滤并且存在图片背景
        const {url: imgUrl = ''} = image;
        if (filter && imgUrl) {
            const isValidFea = filterValidFeature(points);
            if (!isValidFea) {
                notification.warning('请在‘样本图片区域内’进行标注绘制！');
                const toUpdateResetLabel = getLabelWithKey(labels, activeFeature.id);
                if (toUpdateResetLabel && onChangeRef.current) {
                    onChangeRef.current('updateReset', toUpdateResetLabel);
                }
                return;
            }
        }

        const updateLabel = _.find(labels, {key: activeFeature.id});
        if (updateLabel && onChangeRef.current) {
            const newLabel = _.cloneDeep(updateLabel);
            const formatPoints = centerToImagePointsForPoint(points, image);
            newLabel.points = formatPoints;
            if (onChangeRef.current) {
                onChangeRef.current('update', newLabel);
            }
        }
    }, [filter, image, labels, filterValidFeature]);

    // 要素编辑中（即鼠标拖拽矩形或者节点）
    const handleEditingGeometry = useCallback((feature: any, points: Point[]) => {
        const updatingLabel = getLabelWithKey(labels, feature.id);
        const formatPoints = centerToImagePointsForPoint(points, image);
        if (updatingLabel && onChangeRef.current) {
            onChangeRef.current('updating', {...updatingLabel, points: formatPoints});
        }
    }, [image, labels]);

    // feature:hover事件
    const hideHoverLabel = useCallback(() => {
        if (show === 'hover') {
            // 关闭标签名
            const allTexts = gTextLayer.getAllTexts(); // 返回所有文本数据
            const allLabelTexts = _.filter(
                allTexts,
                text => text.id.indexOf(`${LABEL_LABEL_TEXT_ID_PREFIX}`) === 0
            );
            _.each(allLabelTexts, labelText => {
                if (labelText && labelText.visible) {
                    labelText.hide();
                }
            });
        }
    }, [show, gTextLayer]);

    const handleFeatureHover = useCallback((
        feature: any,
        evt: any,
        wxy: any
    ) => {
        if (feature && show === 'hover') {
            hideHoverLabel();
            const featureId = feature.id;
            const labelLabelId = `${LABEL_LABEL_TEXT_ID_PREFIX}${featureId}`;
            const labelText = gTextLayer.getTextById(labelLabelId);
            if (labelText && !labelText.visible) {
                labelText.show();
            }
        }
        else if (!feature) {
            hideHoverLabel();
        }

        if (onChangeRef.current) {
            const labelKey = _.get(feature, 'id');
            const hoverLabel = getLabelWithKey(labels, labelKey);
            onChangeRef.current('hover', hoverLabel, evt, wxy);
        }
    }, [show, labels, gTextLayer, hideHoverLabel]);

    // feature要素删除事件监听
    const handleFeatureDelete = useCallback((__: any, feature: any) => {
        const labelKey = _.get(feature, 'id');
        const toDeleteLabel = getLabelWithKey(labels, labelKey);
        if (toDeleteLabel && onChangeRef.current) {
            onChangeRef.current('delete', toDeleteLabel);
        }
    }, [labels]);

    /**
     * 监听容器id，imageBaseInfo, containerSize是否变化
     * 进行实例化操作
     */
    useEffect(() => {
        gMap && gMap.events.on('featureStatusReset', () => handleFeatureStatusReset());
    }, [gMap, handleFeatureStatusReset]);
    useEffect(() => {
        gMap && gMap.events.on('boundsChanged', () => handleBoundsChanged());
    }, [gMap, handleBoundsChanged]);

    useEffect(() => {
        gMap && gMap.events.on('featureWillSelected', (feature: any) => handleFeatureWillSelected(feature));
    }, [gMap, handleFeatureWillSelected]);

    useEffect(() => {
        gMap && gMap.events.on(
            'geometryDrawDone',
            (type: string, points: Point[], options: any) => handleDrawGeometryDone(type, points, options)
        );
    }, [gMap, handleDrawGeometryDone]);

    useEffect(() => {
        gMap && gMap.events.on('geometryEditDone', (
            type: string,
            activeFeature: any,
            points: Point[]
        ) => {
            if (type === 'mask') {
                handleEditMaskDone(activeFeature);
            }
            else {
                handleEditGeometryDone(activeFeature, points);
            }
        });
    }, [gMap, handleEditGeometryDone, handleEditMaskDone]);

    useEffect(() => {
        gMap && gMap.events.on('geometryEditing', (
            ___: string,
            feature: any,
            points: Point[]
        ) => handleEditingGeometry(feature, points));
    }, [gMap, handleEditingGeometry]);

    useEffect(() => {
        gMap && gMap.events.on('featureHover', (
            feature: any,
            evt: any,
            wxy: any
        ) => handleFeatureHover(feature, evt, wxy));
    }, [gMap, handleFeatureHover]);

    useEffect(() => {
        gMap && gMap.events.on('geometryRemove', (
            type: any,
            feature: any
        ) => {
            if (type === 'point') {
                handleFeatureDelete(type, feature);
            }
        });
    }, [gMap, handleFeatureDelete]);

    useEffect(() => {
        gMap && gMap.events.on('resize', () => update());
    }, [gMap, update]);
};
