/**
 * @file hooks/useTable 表格处理
 * @author xuemin02
 */

import _ from 'lodash';
import {useState, useCallback, useEffect} from 'react';
import {useFetch, useInterval} from 'baidu-acu-react-hooks';
import {useOriginalDeepCopy} from '@huse/previous-value';

export interface TableActionProps {
    action: (params?: any) => any,
    payload?: object,
    interval?: number,
    $onRequest?: (params?: any) => any,
    $onResponse?: (params?: any) => any,
    $onError?: (params?: any) => any,
    abort?: boolean
}

export default (props: TableActionProps) => {
    const {
        action,
        payload = {},
        interval,
        $onRequest,
        $onResponse,
        $onError,
        abort
    } = props;

    const originPayload = useOriginalDeepCopy(payload);

    const [filterData, setFilterData] = useState(payload);
    const [selectedRowKeys, setSelectedRowKeys] = useState([]);

    const $onFetchResponse = useCallback(response => {
        const responsePage = _.isFunction($onResponse)
            ? ($onResponse(response) || response)
            : response;
        const {totalCount, pageNo, result} = responsePage;
        // 特殊逻辑，当totalCount > 0 但是没有数据的时候，查找上一页, 主要场景：最后一页数据全部删除后的情况下兼容
        if (totalCount > 0 && pageNo > 1 && !result.length) {
            setFilterData(prevFilterData => ({...prevFilterData, pageNo: responsePage.pageNo - 1}));
        }
        return responsePage;
    }, [$onResponse]);

    const {data: pageData = {}, loading, reload} = useFetch(
        action,
        filterData,
        $onRequest,
        $onFetchResponse,
        $onError,
        abort
    );

    useInterval(() => reload(false), interval); // 定时刷新

    const {pageNo = 1, pageSize = 10, totalCount = 0, result = []} = pageData as any;

    const handlePaginationChange = useCallback((pageNo, pageSize) => {
        const filterData = {pageNo, pageSize};
        setFilterData(prevFilterData => ({...prevFilterData, ...filterData}));
    }, []);

    const handleSelectedRowKeysChange = useCallback(selectedRowKeys => {
        setSelectedRowKeys(selectedRowKeys);
    }, []);

    const handleTableChange = useCallback((pagination, filters, sorter) => {
        // 这里处理 pagination 变化
        const {current, pageSize: nextPageSize} = pagination;
        const isPaginationChange = current !== pageNo || nextPageSize !== pageSize;
        if (isPaginationChange) {
            handlePaginationChange(current, nextPageSize);
            return;
        }
        // 这里处理 filters 和 sorter 的变化
        const {order, field} = sorter;
        const filterData = {
            pageNo: 1,
            order: order && (order === 'ascend' ? 'asc' : 'desc'),
            orderBy: order && field,
            ...filters
        };
        setFilterData(prevFilterData => ({...prevFilterData, ...filterData}));
    }, [pageNo, pageSize, handlePaginationChange]);

    const loadList = useCallback(() => {
        // 刷新前清空勾选
        setSelectedRowKeys([]);
        reload();
    }, [reload]);

    const loadFirstPage = useCallback(() => {
        // 刷新前清空勾选
        setSelectedRowKeys([]);
        setFilterData(prevFilterData => ({...prevFilterData, pageNo: 1}));
        // 刚好在第一页时需要手动触发一下
        if (pageNo === 1) {
            reload();
        }
    }, [pageNo, reload]);

    useEffect(() => {
        const originPayloadKeys = _.keys(originPayload);
        const originPayloadInFilterData = _.pick(filterData, originPayloadKeys);
        if (!_.isEqual(originPayload, originPayloadInFilterData)) {
            setFilterData(originPayload); // payload中参数变化时，重置整个请求参数后进行请求重发
        }
    }, [originPayload]);

    return {
        pageData,
        loading,
        loadList,
        loadFirstPage,
        filterData,
        setFilterData,
        selectedRowKeys,
        tableProps: {
            loading,
            filterData,
            selectedRowKeys,
            pageNo,
            pageSize,
            totalCount,
            dataSource: result,
            onTableChange: handleTableChange,
            onPaginationChange: handlePaginationChange,
            onPageSizeChange: handlePaginationChange,
            onSelectedRowKeysChange: handleSelectedRowKeysChange
        }
    };
};
