/**
 * @file hooks/useChart.js echart图表处理
 * @author xuemin02
 * @modifyFrom baidu-acu-react-common/hooks/useChart
 */

import React from 'react';
import {useEffect, useCallback, useRef, useMemo} from 'react';
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/pie';
import 'echarts/lib/chart/line';
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/grid';

const COLOR = [
    '#108CEE',
    '#FACC14',
    '#2FC25B',
    '#8543E0',
    '#F04864',
    '#13C2C2',
    '#fa8c16',
    '#a0d911'
];

const makeOptions = (options = {}) => {
    const {color, title = {}, grid = {}, ...restOptions} = options as any;
    return {
        color: color || COLOR,
        title: {
            textStyle: {
                fontSize: 14,
                fontWeight: 'normal'
            },
            ...title
        },
        grid: {
            left: 30,
            right: 30,
            top: 30,
            bottom: 30,
            containLabel: true,
            ...grid
        },
        ...restOptions
    };
};

export default (props = {}) => {
    const {width = '100%', height = 300, options: propsOptions, autoResize = true} = props as any;

    const options = useMemo(() => makeOptions(propsOptions), [propsOptions]);

    const chartInstance = useRef(null);
    const chartRef = useRef(null);

    const chartComp = (
        <div
            ref={chartRef}
            style={{width, height, overflow: 'hidden'}}
        />
    );

    const setAutoResize = useCallback(() => {
        if (autoResize && chartInstance.current && chartRef.current) {
            (chartInstance.current as any).resize();
        }
    }, [autoResize]);

    useEffect(() => {
        if (chartRef.current) {
            const renderedInstance = echarts.getInstanceByDom((chartRef.current as any));
            (chartInstance.current as any) = renderedInstance || echarts.init((chartRef.current as any));
            (chartInstance.current as any).setOption(options);
            (chartInstance.current as any).resize();
        }
    }, [options]);

    useEffect(() => {
        window.addEventListener('resize', setAutoResize);
        return () => {
            chartInstance.current && (chartInstance.current as any).dispose();
            window.removeEventListener('resize', setAutoResize);
        };
    }, [setAutoResize]);

    return {chartComp, chartInstance, chartRef};
};
