/**
 * @file useLabelBox
 * @author dingyang
 */

import _ from 'lodash';
import {useOriginalCopy} from 'baidu-acu-react-hooks';

import useZoom from './zoom';
import useGMap from './gmap';
import useLabel from './label';
import useLabelRemove from './labelRemove';
import useLabelUpdate from './labelUpdate';
import useLabelAdd from './labelAdd';
import useMask from './mask';
import useEagleMap from './eagleMap';
import useMode from './mode';
import useImgGrid from './imgGrid';
import useEvent from './event';
import useMethod from './method';
import {CoreProps, LabelChangeHandler} from './interface';
import {processObjFunction} from './util';

export type ConfigOptions = (...args: any) => CoreProps;

export default (configOptions: ConfigOptions, props: any, handleLabelChange?: LabelChangeHandler) => {
    // options处理
    const formatOptions = configOptions(props);
    const {
        id: outerContainerId,
        wrapClassName,
        container,
        image,
        show = 'default', // 用来展示标签名，默认default，则默认展示，'hover'则至hover时进行展示
        deleteIcon,
        labels = [],
        masks = [],
        mode = 'pan',
        filter = false, // 是否需要进行内置图片内过滤【默认不过滤】
        clip = false, // 是否需要进行内置图片内裁剪【默认不裁剪】，只针对绘制add情况
        drawStyle = null, // 绘制样式【在绘制过程有效】
        eagleMap = false,
        tipsOff // 是否需要关闭编辑态时的tip提示
    } = formatOptions;

    const customCompareEvent = (savedValue: any, value: any) => {
        const savedValueFormat = processObjFunction(savedValue);
        const valueFormat = processObjFunction(value);
        return _.isEqual(savedValueFormat, valueFormat);
    };
    const originalLabels = useOriginalCopy(labels, customCompareEvent);
    const originalImage = useOriginalCopy(image);
    const originalDrawStyle = useOriginalCopy(drawStyle);
    const originalEagleMap = useOriginalCopy(eagleMap);

    // dom容器属性
    const containerId = `gmap-${outerContainerId}`;
    const containerElement = document.getElementById(containerId);
    // 获取zoom相关信息
    const {zoom: initZoom} = useZoom(containerElement, originalImage);

    // gMap实例化操作
    const {
        gMap,
        gImageLayer,
        gFeatureLayer,
        gTextLayer,
        gMaskLayer
    } = useGMap(containerElement, originalImage, initZoom);

    // 相关method方法
    const {
        worldToScreen,
        screenToWorld,
        removeRelatedMarkersAndTexts,
        addRelatedMarkersAndTexts,
        addLabelDeleteIcon,
        removeLabelDeleteIcon,
        handleFeatureStatusReset
    } = useMethod(
        gMap,
        gTextLayer,
        labels,
        originalImage,
        deleteIcon,
        handleLabelChange
    );

    // mode模式切换部分
    useMode(gMap, mode, originalDrawStyle, initZoom, !!tipsOff, handleFeatureStatusReset);

    // imagelayer辅助网格
    useImgGrid(gImageLayer, originalImage);

    // label标注对象
    const {
        toRemoveLabels,
        toUpdateLabels,
        toAddLabels
    } = useLabel(
        gMap,
        originalLabels,
        gFeatureLayer,
        gTextLayer
    );

    useLabelRemove(
        gMap,
        gFeatureLayer,
        gTextLayer,
        toRemoveLabels,
        removeRelatedMarkersAndTexts
    );
    useLabelUpdate(
        gMap,
        gFeatureLayer,
        gTextLayer,
        originalImage,
        toUpdateLabels,
        removeRelatedMarkersAndTexts,
        addRelatedMarkersAndTexts,
        addLabelDeleteIcon,
        removeLabelDeleteIcon
    );
    useLabelAdd(
        gFeatureLayer,
        gTextLayer,
        originalImage,
        show,
        toAddLabels,
        addRelatedMarkersAndTexts,
        addLabelDeleteIcon
    );

    useMask(gMap, masks, originalImage, gMaskLayer);

    // eagleMap缩略图部分
    useEagleMap(gMap, originalEagleMap, originalImage);

    // gMap实例对象事件绑定
    useEvent(
        gMap,
        originalLabels,
        gTextLayer,
        originalImage,
        show,
        filter,
        clip,
        handleLabelChange,
        handleFeatureStatusReset
    );

    const labelProps = {outerContainerId, containerId, wrapClassName, container};
    return {
        labelProps,
        gMap,
        initZoom,
        gImageLayer,
        gFeatureLayer,
        gTextLayer,
        tools: {
            worldToScreen,
            screenToWorld
        }
    };
};
