/**
 * @file 标注组件-标注删除
 * @author dingyang(dingyang@baidu.com)
 */

import _ from 'lodash';
import {useEffect, useCallback} from 'react';

import {
    LABEL_DELETE_MARKER_ID_PREFIX,
    LABEL_LABEL_TEXT_ID_PREFIX
} from './constants';
import {Label} from './interface';

export default (
    gMap: any,
    gFeatureLayer: any | null,
    gTextLayer: any | null,
    toRemoveLabels: Label[],
    removeRelatedMarkersAndTexts: (arg0: Label) => void
) => {
    // 处理待删除label
    const removeLabels = useCallback(() => {
        if (!gMap || !gFeatureLayer || !gTextLayer) {
            return;
        }
        _.each(toRemoveLabels, removeLabel => {
            const {key: featureId} = removeLabel;
            const deleteMarkerId = `${LABEL_DELETE_MARKER_ID_PREFIX}${featureId}`;
            const labelLabelId = `${LABEL_LABEL_TEXT_ID_PREFIX}${featureId}`;
            // 依次删除相关label/delete-icon/标签名
            gFeatureLayer.removeFeatureById(featureId); // 执行选中元素删除
            gTextLayer.removeTextById(labelLabelId); // 对应删除其标签文字
            gMap.mLayer.removeMarkerById(deleteMarkerId); // 对应删除标注层中删除（x）icon
            // 删除关联texts&markers
            removeRelatedMarkersAndTexts(removeLabel);
        });
    }, [
        gMap,
        gFeatureLayer,
        gTextLayer,
        toRemoveLabels,
        removeRelatedMarkersAndTexts
    ]);

    useEffect(() => {
        removeLabels();
    }, [toRemoveLabels]);
};
