import {notification} from 'antd';

const defaultDuration = 3;

export default {
    error: (message: string, duration = defaultDuration) => notification.error({message, duration}),
    success: (message: string, duration = defaultDuration) => notification.success({message, duration}),
    info: (message: string, duration = defaultDuration) => notification.info({message, duration}),
    warning: (message: string, duration = defaultDuration) => notification.warning({message, duration})
};
