/**
 * @file BottomSubmit 表单底部提交
 * @author xuemin02
 */

import React from 'react';
import _ from 'lodash';
import {Button} from 'antd';

import './index.less';

export interface BottonSubmitProps {
    options: Array<any>
}

export default (props: BottonSubmitProps) => {

    const {options} = props;

    return (
        <div className="aibp-bottom-submit">
            {_.map(options, option => {
                const {text, ...buttonProps} = option;
                return <Button {...buttonProps}>{text}</Button>;
            })}
        </div>
    );

};
