/**
 * @file components/FilterDropdown
 * @author xuemin02
 */

import _ from 'lodash';
import React, {useState, useCallback, ReactNode, useMemo, useEffect} from 'react';
import {Menu, Input, Checkbox} from 'antd';

import {EMPTY_DATA_IMG} from '../../constants';

import './index.less';

const Search = Input.Search;
const PREFIX_CLASS = 'ant-table-filter';
const DROPDOWN_PREFIX_CLASS = 'ant-dropdown';

export interface localeProps {
    filterConfirm?: string | ReactNode
    filterReset?: string | ReactNode
}

export interface FilterDropdownProps {
    // 是否是多选
    multiple?: boolean,
    // 显示搜索框，仅多选时有效
    showSearch?: boolean,
    // 显示全选/全不选，仅多选时有效
    showSelectAll?: boolean,
    placeholder?: string,
    locale?: localeProps,
    selectedKeys?: Array<string>,
    getPopupContainer?: any,
    filters: Array<object>,
    confirm?: () => void,
    setSelectedKeys: (selectedKeys: any) => void
}

const isSubstr = (str: string, substr: string) => {
    return str.toLowerCase().indexOf(substr.toLowerCase()) > -1;
}

export default (props: FilterDropdownProps) => {

    const {
        multiple = true,
        showSearch = true,
        showSelectAll = true,
        placeholder = '请输入',
        locale = {},
        selectedKeys = [],
        filters,
        confirm,
        getPopupContainer,
        setSelectedKeys
    } = props;

    const {filterConfirm = '确定', filterReset = '重置'} = locale;

    const [searchKey, setSearchKey] = useState();
    const [selectedItemKeys, setSelectedItemKeys] = useState(selectedKeys);

    const handleSearchChange = useCallback(e => setSearchKey(e.target.value), []);

    const handleSelectAllChange = useCallback(selectAll => {
        selectAll ? handleMenuAllDeselect() : handleMenuAllSelect();
    }, []);

    const handleMenuItemSelect = useCallback(item => {
        const itemValue = _.get(item, 'item.props.data-value', '');
        if (itemValue || _.isNumber(itemValue)) {
            setSelectedItemKeys(prevSelectedItemKeys => {
                return multiple ? [...prevSelectedItemKeys, itemValue] : [itemValue]
            });
        }
    }, []);

    const handleMenuItemDeselect = useCallback(item => {
        const itemValue = _.get(item, 'item.props.data-value', '');
        if (itemValue || _.isNumber(itemValue)) {
            const currentSelectedItemKeys = _.filter(selectedItemKeys, (selectedItemKey: any) => selectedItemKey !== itemValue);
            setSelectedItemKeys(currentSelectedItemKeys);
        }
    }, [selectedItemKeys]);

    const handleMenuAllSelect = useCallback(() => {
        const selectedItemKeys = _.map(filters, 'value');
        setSelectedItemKeys(selectedItemKeys);
    }, []);

    const handleMenuAllDeselect = useCallback(() => setSelectedItemKeys([]), []);

    const handleConfirm = useCallback(() => {
        confirm && confirm();
    }, [confirm]);

    const handleClearFilters = useCallback(() => {
        setSelectedItemKeys([]);
        window.setTimeout(() => {
            confirm && confirm();
        }, 300)
    }, [confirm]);

    useEffect(() => {
        setSelectedKeys(selectedItemKeys);
        // 只对单选做立即触发查询，多选需手动触发查询
        !multiple && confirm && confirm();
    }, [selectedItemKeys]);

    const menuOptions = useMemo(() => {
        if (!searchKey) {
            return filters;
        }
        return _.filter(filters, (filterItem: any) => {
            const {value, text} = filterItem;
            // `${value}` 兼容数字情况
            // searchKey || ''，兼容undefined情况。虽然走到这里 searchKey 一定有值，但打包的时候会报类型错误。。。
            return isSubstr(`${value}`, searchKey || '') || isSubstr(`${text}`, searchKey || '');
        });
    }, [searchKey, filters]);

    const selectAll = multiple ? filters.length === selectedItemKeys.length : selectedItemKeys.length === 0;

    // 将selectedItemKeys转换为string
    const formatSelectedItemKeys = _.map(selectedItemKeys, selectedItemKey => `${selectedItemKey}`);

    return (
        <div className="aibp-table-filter-dropdown">
            {multiple && showSearch && (
                <div className="aibp-table-filter-dropdown-search">
                    <Search
                        placeholder={placeholder}
                        onChange={handleSearchChange}
                    />
                </div>
            )}
            {multiple ? showSelectAll && menuOptions.length > 0 && (
                <div
                    className="aibp-table-filter-dropdown-select-all"
                    onClick={() => handleSelectAllChange(selectAll)}
                >
                    <Checkbox checked={selectAll} />
                    <span>全选/全不选</span>
                </div>
            ) : (
                <Menu
                    getPopupContainer={getPopupContainer}
                    prefixCls={`${DROPDOWN_PREFIX_CLASS}-menu`}
                    selectedKeys={formatSelectedItemKeys.length ? [] : ['all']}
                    onSelect={handleMenuAllDeselect}
                >
                    <Menu.Item key="all"><span>全部</span></Menu.Item>
                </Menu>
            )}
            <Menu
                multiple={multiple}
                getPopupContainer={getPopupContainer}
                prefixCls={`${DROPDOWN_PREFIX_CLASS}-menu`}
                selectedKeys={formatSelectedItemKeys}
                onSelect={handleMenuItemSelect}
                onDeselect={handleMenuItemDeselect}
            >
                {_.map(menuOptions, (menuItem: any) => {
                    const {value, text} = menuItem;
                    // value 赋值在 key 上，会被强制转换为 string， 当value为数字时会出错
                    // 因此再次赋值在 data-value 上
                    return (
                        <Menu.Item key={value} data-value={value}>
                            {multiple && (
                                <Checkbox checked={selectedItemKeys.indexOf(value) >= 0} />
                            )}
                            <span>{text}</span>
                        </Menu.Item>
                    );
                })}
            </Menu>
            {multiple && menuOptions.length === 0 && (
                <div className="ant-empty ant-empty-normal aibp-empty">
                    <img className="aibp-empty-image" src={EMPTY_DATA_IMG} width={100} />
                    <div className="ant-empty-description aibp-empty-description">
                        暂无数据
                    </div>
                </div>
            )}
            {multiple && (
                <div className={`${PREFIX_CLASS}-dropdown-btns`}>
                    <a className={`${PREFIX_CLASS}-dropdown-link confirm`} onClick={handleConfirm}>
                        {filterConfirm}
                    </a>
                    <a className={`${PREFIX_CLASS}-dropdown-link clear`} onClick={handleClearFilters}>
                        {filterReset}
                    </a>
                </div>
            )}
        </div>
    );

};
