/**
 * @file 标注组件-gMap实例对象模式切换
 * @author dingyang(dingyang@baidu.com)
 */

import {useEffect, useCallback} from 'react';
import AILabel from 'ailabel';

import {Mode} from './interface';
import {GFEATURE_STYLE} from './constants';

export default (
    gMap: any,
    mode: Mode,
    drawStyle: any,
    initZoom: number,
    tipsOff: boolean,
    handleFeatureStatusReset: any
) => {
    const handleModeChange = useCallback(() => {
        // mode 此处有个潜规则【即mode不在枚举中，即为禁止平移缩放，待后续修改】
        if (gMap) {
            handleFeatureStatusReset();
            gMap.setMode(mode);
        }
    }, [gMap, mode, handleFeatureStatusReset]);

    // 绘制样式变化触发
    const handleDrawStyleChange = useCallback(() => {
        if (gMap) {
            const cDrawStyle = new AILabel.Style(drawStyle || GFEATURE_STYLE);
            gMap.setGlobalStyle(cDrawStyle);
        }
    }, [gMap, drawStyle]);

    // 编辑态时tip提示开关
    const handleTipsOffChange = useCallback(() => {
        if (gMap) {
            const showOrHideFuncName = tipsOff ? 'hideTips' : 'showTips';
            gMap.tipLayer[showOrHideFuncName]();
        }
    }, [gMap, tipsOff]);

    // 监听gMap、mode变化触发 设置当前gMap模式
    useEffect(() => {
        handleModeChange();
    }, [handleModeChange]);

    useEffect(() => {
        handleDrawStyleChange();
    }, [handleDrawStyleChange]);

    useEffect(() => {
        handleTipsOffChange();
    }, [handleTipsOffChange]);

    // initZoom变化，此时不需要重新实例化，直接对gMap进行centerAndZoom即可
    useEffect(() => {
        if (gMap) {
            gMap.centerAndZoom({x: 0, y: 0}, initZoom);
        }
    }, [gMap, initZoom]);
};
