/**
 * @file 标注组件-标注更新
 * @author dingyang(dingyang@baidu.com)
 */

import _ from 'lodash';
import {useCallback, useEffect} from 'react';
import AILabel from 'ailabel';

import {imageToCenterPointsForPoint, getLabelLabelDeletePosition} from './util';
import {
    LABEL_DELETE_MARKER_ID_PREFIX,
    LABEL_LABEL_TEXT_ID_PREFIX,
    GFEATURE_STYLE,
    GTEXT_STYLE
} from './constants';
import {Label, Image, Point} from './interface';

export default (
    gMap: any,
    gFeatureLayer: any | null,
    gTextLayer: any | null,
    image: Image | any,
    toUpdateLabels: Label[],
    removeRelatedMarkersAndTexts: (arg0: Label) => void,
    addRelatedMarkersAndTexts:(featureId: any, label: Label) => void,
    addLabelDeleteIcon: (featureId: any, label: Label) => void,
    removeLabelDeleteIcon: (featureId: any) => void
) => {
    // 更新标注框对应的文本及删除icon
    const updateLabelAndDeletePosition = useCallback((featureType: string, featureId: any, points: Point[]) => {
        const labelPoint = getLabelLabelDeletePosition(featureType, 'label', points);
        const deletePoint = getLabelLabelDeletePosition(featureType, 'delete', points);

        const deleteMarkerId = `${LABEL_DELETE_MARKER_ID_PREFIX}${featureId}`;
        const labelLabelId = `${LABEL_LABEL_TEXT_ID_PREFIX}${featureId}`;

        const deleteMarker = gMap.mLayer.getMarkerById(deleteMarkerId);
        const labelText = gTextLayer.getTextById(labelLabelId);

        if (deleteMarker) {
            deleteMarker.update(deletePoint);
        }
        if (labelText) {
            labelText.update({pos: labelPoint});
        }
    }, [gMap, gTextLayer]);

    // 处理待更新label
    const updateLabels = useCallback(() => {
        _.each(toUpdateLabels, updateLabel => {
            const {
                key: featureId,
                select = false,
                label: labelConfig = {},
                points = [],
                style
            } = updateLabel;

            const {name: labelName = '', style: labelStyle} = labelConfig;
            const labelLabelId = `${LABEL_LABEL_TEXT_ID_PREFIX}${featureId}`;
            const cLabelFeature = gFeatureLayer.getFeatureById(featureId);
            const cLabelText = gTextLayer.getTextById(labelLabelId);

            const cFeatureStyle = new AILabel.Style(style || GFEATURE_STYLE);
            const cLabelStyle = new AILabel.Style(labelStyle || GTEXT_STYLE);

            if (!cLabelFeature || !cLabelText) {
                return;
            }

            const formatPoints = imageToCenterPointsForPoint(points, image);
            cLabelFeature.update({points: formatPoints, style: cFeatureStyle});
            if (select) {
                cLabelFeature.active();
                addLabelDeleteIcon(featureId, updateLabel);
            }
            else if (!select && cLabelFeature.activeStatus) {
                cLabelFeature.deActive();
                removeLabelDeleteIcon(featureId);
            }
            cLabelText.update({text: labelName, style: cLabelStyle});

            updateLabelAndDeletePosition(cLabelFeature.type, cLabelFeature.id, cLabelFeature.points);

            // 首先删除其关联的下属texts和markers对象集合,再执行添加
            removeRelatedMarkersAndTexts(updateLabel);
            addRelatedMarkersAndTexts(featureId, updateLabel);
        });
    }, [
        gFeatureLayer,
        gTextLayer,
        toUpdateLabels,
        image,
        addLabelDeleteIcon,
        removeLabelDeleteIcon,
        addRelatedMarkersAndTexts,
        removeRelatedMarkersAndTexts,
        updateLabelAndDeletePosition
    ]);

    useEffect(() => {
        updateLabels();
    }, [toUpdateLabels]);
};
