/**
 * @file ColorPicker
 * @author xuemin02
 */

import React, {useState, useCallback, useMemo, useEffect} from 'react';
import {Popover} from 'antd';
import {SketchPicker} from 'react-color';

import {PRIMARY_COLOR} from '../../constants';

const noop = () => {};

export interface ColorPickerProps {
    color?: string;
    small?: boolean;
    onChange?: (hex: string, color: {hex: string}) => void;
    onChangeComplete?: (hex: string) => void;
}

export default (props: ColorPickerProps) => {
    const {
        color: propsColor = PRIMARY_COLOR,
        small = false,
        onChange = noop,
        onChangeComplete = noop,
        ...restProps
    } = props;
    const [color, setColor] = useState(propsColor);

    const handleColorChange = useCallback(color => {
        setColor(color.hex);
        onChange(color.hex, color);
    }, []);
    const handleChangeComplete = useCallback(color => {
        setColor(color.hex);
        onChangeComplete(color.hex);
    }, []);

    useEffect(() => {
        if (color !== propsColor) {
            setColor(propsColor);
        }
    }, [propsColor]);

    const styles = useMemo(() => (
        {
            color: {
                width: small ? '80px' : '120px',
                height: small ? '16px' : '24px',
                borderRadius: '2px',
                background: color
            },
            swatch: {
                padding: '4px',
                background: '#fff',
                borderRadius: '2px',
                boxShadow: '0 0 0 1px rgba(0, 0, 0, .1)',
                display: 'inline-block',
                cursor: 'pointer'
            }
        }
    ), [color]);

    const swatch = (
        <div style={styles.swatch}>
            <div style={styles.color} />
        </div>
    );

    const picker = (
        <SketchPicker
            {...restProps}
            color={color}
            onChange={handleColorChange}
            onChangeComplete={handleChangeComplete}
        />
    );

    return (
        <Popover
            overlayClassName="color-popover"
            title={false}
            trigger="click"
            content={picker}
        >
            {swatch}
        </Popover>
    );
};
