/**
 * @file hooks/useDialog 各种「对话框」的处理
 * @author xuemin02
 */

import _ from 'lodash';
import React, {useState, useCallback, ReactNode} from 'react';
import {Modal} from 'antd';
import {ExclamationCircleOutlined} from '@ant-design/icons';

const {confirm} = Modal;

// 调用子组件的提交，这里不能放在state里，会引起无限重绘
let onSubmit:any;

export interface DialogConfigProps {
    type?: 'ajax' | 'modal' | 'drawer',
    content?: string | ReactNode,
    action?: (params?: any) => any,
    config?: object,
    $onRequest?: (params?: any) => any,
    $onResponse?: (params?: any) => any,
    $onError?: (params?: any) => any
}

export default () => {
    const [dialogProps, setDialogProps] = useState({});

    const {
        dialogContent,
        onOk,
        onCancel,
        onClose,
        $onRequest,
        $onResponse,
        $onError
    } = dialogProps as any || {};

    if (typeof dialogContent === 'object') {
        dialogContent.props = {
            onSubmit(submit: any) {
                onSubmit = submit;
            },
            ...dialogContent.props
        };
    }

    const handleDialog = (dialogConfig: DialogConfigProps = {}) => {
        const {
            type,
            content,
            action = () => Promise.resolve(),
            config,
            $onRequest,
            $onResponse,
            $onError
        } = dialogConfig;
        switch (type) {
            case 'ajax':
                setDialogProps({});
                confirm({
                    icon: <ExclamationCircleOutlined />,
                    content,
                    cancelText: '取消',
                    okText: '确定',
                    ...config,
                    onOk() {
                        const params = _.isFunction($onRequest) && $onRequest();
                        return action(params)
                            .then((response:any) => {
                                _.isFunction($onResponse) && $onResponse(response);
                            }).catch((error:any) => {
                                $onError && $onError(error);
                            });
                    }
                });
                break;
            case 'modal':
                setDialogProps({
                    type,
                    // 深拷贝一下，不然demo展示会报错
                    dialogContent: _.cloneDeep(content),
                    visible: true,
                    destroyOnClose: true,
                    maskClosable: false,
                    $onRequest,
                    $onResponse,
                    $onError,
                    ...config
                });
                break;
            case 'drawer':
                setDialogProps({
                    type,
                    dialogContent: _.cloneDeep(content),
                    visible: true,
                    destroyOnClose: true,
                    maskClosable: false,
                    ...config
                });
                break;
            default:
                setDialogProps({});
                break;
        }
    };

    const handleDialogCompClose = useCallback(() => {
        setDialogProps(prevDialogProps => ({...prevDialogProps, visible: false}));
    }, []);

    const handleDialogCompCancel = useCallback(() => {
        const actionCancelFn = onCancel || onClose;
        actionCancelFn ? actionCancelFn() : handleDialogCompClose();
    }, [onCancel, onClose, handleDialogCompClose]);

    const handleDialogCompOk = useCallback(() => {
        _.isFunction($onRequest) && $onRequest();
        const actionOkFn = onOk || onSubmit;
        // 返回 promise 可以延迟关闭
        if (actionOkFn) {
            const actionOkResult = actionOkFn();
            if (actionOkResult && actionOkResult.then) {
                setDialogProps(prevDialogProps => ({...prevDialogProps, confirmLoading: true}));
                actionOkResult
                    .then((response:any) => {
                        _.isFunction($onResponse) && $onResponse(response);
                        handleDialogCompClose();
                    })
                    .catch((error:any) => {
                        $onError && $onError(error);
                        setDialogProps(prevDialogProps => ({...prevDialogProps, confirmLoading: false}));
                    });
            }
            else {
                _.isFunction($onResponse) && $onResponse();
                handleDialogCompClose();
            }
        }
        else {
            _.isFunction($onResponse) && $onResponse();
            handleDialogCompClose();
        }
    }, [onOk, $onRequest, $onResponse, $onError, handleDialogCompClose]);

    return {
        handleDialog,
        dialogProps: {
            ...dialogProps,
            onActionCompOk: handleDialogCompOk,
            onActionCompCancel: handleDialogCompCancel
        },
        handleDialogCompClose
    };
};
