/**
 * @file ExpandIcon 展开收起时的icon（带有动画效果）
 * @author xuemin02
 */

import React from 'react';
import {DownOutlined} from '@ant-design/icons';
import {useState, useCallback, useEffect} from 'react';

import './index.less';

export interface ExpandIconProps {
    className?: string,
    text?: string | React.ReactNode,
    expand?: boolean,
    onExpand?: (params?: any) => any
}

export default (props: ExpandIconProps) => {
    const {className = '', text, expand: propsExpand, onExpand} = props;

    const [expand, setExpand] = useState(!!propsExpand);

    const handleExpandChange = useCallback(() => {
        const newExpand = !expand;
        setExpand(newExpand);
        onExpand && onExpand(newExpand);
    }, [expand, onExpand]);

    useEffect(() => {
        if ((!!propsExpand) !== expand) {
            setExpand(!!propsExpand);
        }
    }, [propsExpand]);

    return (
        <div
            className={`aibp-expand-icon ${expand ? 'aibp-expand-icon-active' : ''} ${className}`}
            onClick={handleExpandChange}
        >
            {text && <span className="expand-text">{text}</span>}
            <DownOutlined className="expand-arrow" />
        </div>
    );
};
